
$(document).ready(function () {
  // bind change event to select
  $('#language-label').on('change', function () {
    var homeName ="index"
    var url = new URL(window.location.href);
    var urlString = window.location.href;
    var modifier = $(this).val(); // get selected value
    var newUrl;
    console.log(urlString);

    if (urlString == "http://localhost:2368/")
    {newUrl = urlString;
    window.location = newUrl + modifier + '/' ;
    }

    else if (modifier == 'es') {
        newUrl = urlString.replace(/en/, modifier);
        window.location = newUrl.slice(0, -4);}

    else if (modifier == 'en') {
        newUrl = urlString.replace(/es/, modifier)
        window.location = newUrl.slice(0, -1) + '-' + modifier;}    

  });
});
